import React, { useState, useEffect } from "react";
import "./animation.scss";

export default (props) => {
  const [inlineStyle, setInlineStyle] = useState({});
  let ref = React.useRef(props.index);
  ref.current = props.index;

  useEffect(() => {
    if (props.fadeIn) {
      setInlineStyle({ animation: `fadeIn ease-in ${props.speed}` });
    }
    if (props.fadeOut) {
      const timer = setTimeout(() => {
        setInlineStyle({
          animation: `fadeOut ease-in ${props.speed} forwards`,
        });
      }, props.fadeOut);
      return () => clearTimeout(timer);
    }
  }, [props.fadeIn, props.fadeOut, props.speed, props.index]);
  return <div style={inlineStyle}>{props.children}</div>;
};
