import React from "react";
import { connect } from "react-redux";
import * as utils from "@lib/utils";
import { Header } from "../../components/header";
import { ComponentSwitch } from "../../components/ui";
import HomeDefault from "./HomeDefault";
import About from "../about/About";
import { Projects } from "../";
import "./home.scss";
import * as Lib from "../../lib/utils";

interface IProps {
  title: string;
}

interface IState {
  activeComponent: string;
  activeClassname: string;
}

class Home extends React.PureComponent<IProps, IState> {
  state = {
    activeComponent: "Home",
    activeClassname: "inner-container slider slide-up-end",
  };

  constructor(props) {
    super(props);
    this.setMainDisplay = this.setMainDisplay.bind(this);
  }
  componentDidMount() {
    utils.setWindowTitle(this.props.title);
  }

  setMainDisplay(activeComponent) {
    if (activeComponent === "Home") {
      this.slideUp(activeComponent);
    } else {
      this.fadeOutAndIn(activeComponent);
    }
  }

  setActiveComponent(activeComponent) {
    return new Promise((resolve) => {
      this.setState({ activeComponent }, () => {
        resolve();
      });
    });
  }

  async fadeOutAndIn(activeComponent) {
    await this.setActiveClassname("inner-container fade fade-out", 400);
    await this.setActiveComponent(activeComponent);
    await this.setActiveClassname("inner-container fade fade-in");
    return new Promise((resolve) => resolve());
  }

  async slideUp(activeComponent) {
    this.setState(
      {
        activeComponent,
        activeClassname: "slide-up-start",
      },
      async () => {
        await Lib.pause(200);
        this.setActiveClassname("inner-container slider slide-up-end");
      }
    );
  }

  async setActiveClassname(activeClassname, time = 50) {
    return new Promise((resolve) => {
      this.setState(
        {
          activeClassname,
        },
        async () => {
          await Lib.pause(time);
          return resolve();
        }
      );
    });
  }

  render() {
    return (
      <div className="outer-container">
        <Header setMainDisplay={this.setMainDisplay} />
        <div className={`${this.state.activeClassname}`}>
          <ComponentSwitch value={this.state.activeComponent}>
            <HomeDefault caseValue="Home" />
            <About caseValue="About" />
            <Projects caseValue="Projects" />
          </ComponentSwitch>
        </div>
      </div>
    );
  }
}

const mapState = ({ appReducer }) => ({
  title: appReducer.title,
});

export default connect(mapState)(Home);
