import React, { useEffect, useState, useCallback } from "react";
import { JSLogo, NodeJS, PHPLogo, ReactLogo } from "../../assets/images";
import Fade from "../../components/animation/Fade";

interface ILanguage {
  image: any;
  caption: string;
}

interface ILanguages extends Array<ILanguage> {}

export default () => {
  const languages: ILanguages = [
    { image: JSLogo, caption: "Javascript : 16 years of experience" },
    { image: ReactLogo, caption: "React : 5 years of experience" },
    { image: NodeJS, caption: "NodeJS : 5 years of experience" },
    { image: PHPLogo, caption: "PHP : 7 years of experience" },
  ];

  const [index, setIndex] = useState(0);

  const preloadImages = useCallback(() => {
    languages.forEach((language) => {
      let image = new Image();
      image.src = language.image;
    });
    return;
  }, [languages]);

  useEffect(() => {
    const timer = setTimeout(() => {
      let idx = index + 1;
      if (idx === languages.length) {
        idx = 0;
      }
      setIndex(idx);
    }, 3500);
    return () => clearTimeout(timer);
  }, [index, languages.length, preloadImages]);

  return (
    <Fade fadeIn={"0"} fadeOut={3000} speed={".3s"} index={index}>
      <div className="home-image-container">
        <div>
          <img
            src={languages[index].image}
            className="home-img"
            alt={languages[index].caption}
          />
        </div>
        <div>{languages[index].caption}</div>
      </div>
    </Fade>
  );
};
