import React, { useState } from "react";
import LinkedInImg from "../../assets/images/LinkedIn.svg";

export default ({ setMainDisplay }) => {
  const [current, setCurrent] = useState("Home");
  let menuItems = ["Home", "About", "Projects", "Contact"];
  const updateDisplay = (item) => {
    setCurrent(item);
    setMainDisplay(item);
  };

  return (
    <div className="flex">
      {menuItems.map((item) => {
        let className = current === item ? "menu-item-active" : "menu-item";
        return (
          <div
            className={className}
            key={item}
            onClick={() => updateDisplay(item)}
          >
            {item}
          </div>
        );
      })}
      <LinkedInImg width={24} height={24} fill="white" />
    </div>
  );
};
